.near-end-items-list-body{
    background-color: #e0e0e0;
    padding: 1rem;
    border-radius: 1rem;
    width: 33rem;
    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.25);
}
.near-end-items-list-body > h1 {
    font-family: "Rubik";
    font-style: normal;
    font-weight: 600;
    font-size: 1.5rem;
    line-height: 140%;
  
    /* or 34px */
  
    color: #2f2e41;
  }
  .near-end-items-list-body > div {
    overflow-y: auto;
    overflow-x: hidden;
    height: 20rem;
  }